import { Component, OnInit, ViewChild } from '@angular/core';
import { rowsAnimation } from '../animations/template.animations';
import { FormBuilder, FormControl, FormGroup, FormArray, Validators, NgForm } from '@angular/forms';
import { UserService } from '../services/user/user.service';
import { MatTableDataSource } from '@angular/material/table';
import { MaterialGatePass } from '../models/gatePass';
import { Department } from '../models/department';
import { GpArray } from '../models/gpArray'
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Create } from '../models/create';
import { Gate } from '../models/gate';
import { Unit } from '../models/unit';
import { Project } from '../models/project';

interface Type {
  viewValue: string;
}
interface Owner {
  viewValue: string;
}
interface Movement {
  viewValue: string;
}
// interface Gate {
//   viewValue: string;
// }

@Component({
  selector: 'app-gate-pass',
  templateUrl: './gate-pass.component.html',
  styleUrls: ['./gate-pass.component.css'],
  animations: [rowsAnimation]
})
export class GatePassComponent implements OnInit {


  createForm: Create = {
    name: this.userService.loggedIn.name,
    selectedGate: null,
    selectedDepartment: this.userService.loggedIn.selectedDepartment,
    selectedProject: this.userService.loggedIn.selectedProject,
    selectedType: null,
    selectedOwner: null,
    selectedMovement: null,
    receiver: null,
    firm: null,
    vendorName: null,
  };

  dcGatePass = ['sno', 'modeOfTransport', 'description', 'quantity', 'unit', 'issuedTo', 'from', 'to', 'reason', 'remark', 'dateOfReturn'];
  dsGatePass: MatTableDataSource<MaterialGatePass>;
  gatePasses: MaterialGatePass[] = [];

  // gatePassForm;
  constructor(private userService: UserService, private fb: FormBuilder, private _snackBar: MatSnackBar, private router: Router) {
    // this.gatePasses.push(this.createNewGatePass(1));
    this.dsGatePass = new MatTableDataSource(this.gatePasses);
    // this.gatePassForm = this.fb.group({
    //   selectedGate: new FormControl('', Validators.required),
    //   selectedType: new FormControl('', Validators.required),
    //   selectedOwner: new FormControl('', Validators.required),
    //   selectedMovement: new FormControl('', Validators.required),
    //   receiver: new FormControl('', Validators.required),
    //   firm: new FormControl('', Validators.required),
    //   sno: new FormControl('', Validators.required),
    //   modeOfTransport: new FormControl('', Validators.required),
    //   description: new FormControl('', Validators.required),
    //   quantity: new FormControl('', Validators.required),
    //   unit: new FormControl('', Validators.required),
    //   issuedTo: new FormControl('', Validators.required),
    //   from: new FormControl('', Validators.required),
    //   to: new FormControl('', Validators.required),
    //   reason: new FormControl('', Validators.required),
    //   remark: new FormControl('', Validators.required),
    //   dateOfReturn: new FormControl('', Validators.required),
    // })
  }

  ngOnInit(): void {
    this.gatePasses.push(this.createNewGatePass(1));
    this.getProjects();
    this.getDepartments();
    this.getUnits();
    // this.disableInput();
  }

  setGates() {
    const selectedProject = this.projects.find(project => project._id = this.userService.loggedIn.selectedProject._id)
    this.gates = selectedProject.gates
  }

  // disableInput() {
  //   this.createForm.get('name').disable();
  // }
  selectedUnit: string;
  selectedType: string;
  selectedOwner: string;
  selectedMovement: string;
  selectedGate: string;
  selectedDepartment: string;

  types: Type[] = [
    { viewValue: 'Returnable' },
    { viewValue: 'Non-Returnable' },
  ];
  owners: Owner[] = [
    { viewValue: 'NTPC' },
    { viewValue: 'Vendor' },
  ];
  movements: Movement[] = [
    { viewValue: 'In' },
    { viewValue: 'Out' },
    { viewValue: 'Within' },
  ];
  // gates: Gate[] = [
  //   { viewValue: 'Gate No. 1' },
  //   { viewValue: 'Gate No. 2' },
  //   { viewValue: 'Gate No. 3' },
  // ];

  Units: Unit[];
  departments: Department[];
  gates: Gate[];
  projects: Project[];

  getDepartments() {
    this.userService.getDepartments()
      .subscribe(departments => this.departments = departments);
  }

  getUnits() {
    this.userService.getUnits()
      .subscribe(units => this.Units = units);
  }

  getProjects() {
    this.userService.getProjects()
      .subscribe(projects => {
        this.projects = projects;
        this.setGates();
      })
  }

  dcDate;
  dateFunc(value) {
    console.log(value)
    // this.createForm.controls.selectedType.value
    for (const dc of this.dcGatePass) {
      if (dc == "dateOfReturn") {
        this.dcDate = true;
        break;
      }
      else {
        this.dcDate = false;
      }
    }
    if (value === "Non-Returnable" && this.dcDate === true) {
      this.dcGatePass.pop();
      this.gatePasses.forEach(gatePass => {
        gatePass.dateOfReturn = null;
      })
    }
    else if (value === "Returnable" && this.dcDate === false) {
      this.dcGatePass.push('dateOfReturn');
    }

  }

  createNewGatePass(sno: number): MaterialGatePass {
    return {
      sno: sno,
      modeOfTransport: null, description: null, quantity: null, unit: null,
      issuedTo: null, dateOfReturn: null, from: null,
      to: null, reason: null, remark: null, incomingRef: null
    };
  }

  addRow() {
    this.dsGatePass.data.push(this.createNewGatePass(this.dsGatePass.data.length + 1));
    this.dsGatePass.filter = "";
  }

  deleteRow() {
    if (this.dsGatePass.data.length == 1) {
      return;
    }
    this.dsGatePass.data.pop();
    this.dsGatePass.filter = "";
  }

  gpArray: GpArray = { gatePassNo: 0, createForm: null, createdBy: null, authorized: null, authorizedDate: null, status: 'Created', gatePass: [] }
  onSubmitFunc() {
    if (this.gatePasses === [] || !this.createForm) {
      alert("Please fill all the fields");
    }
    else {
      this.gpArray.createForm = this.createForm
      this.gpArray.gatePass = this.gatePasses
      this.gpArray.createdBy = this.userService.loggedIn
      this.userService.addGpArray(this.gpArray as GpArray)
        .subscribe((gpArray) => {
          // alert("Form Submitted")
          this._snackBar.openFromComponent(GpCreatedComponent, {
            duration: 3 * 1000,
          });
          this.userService.selectedGpArray = gpArray;
          this.userService.storeGpArray(gpArray.gatePassNo);
          this.router.navigate(['return']);
        })
      console.log(this.gpArray)
    }
  }

}
