import { Component, OnInit, Input } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import {
  debounceTime, distinctUntilChanged, switchMap
} from 'rxjs/operators';
import { GpArray } from '../models/gpArray';
import { UserService } from '../services/user/user.service';
import { Router } from '@angular/router';
import { PrintService } from '../services/print/print.service';
@Component({
  selector: 'app-gatepass-search',
  templateUrl: './gatepass-search.component.html',
  styleUrls: ['./gatepass-search.component.css']
})
export class GatepassSearchComponent implements OnInit {

  gatePasses$: Observable<GpArray[]>;
  private searchTerms = new Subject<string>();
  @Input() print: string;
  @Input() project: string;

  constructor(private userService: UserService, private router: Router, private printService: PrintService) { }

  search(term: string): void {
    this.searchTerms.next(term);
  }

  ngOnInit(): void {
    this.gatePasses$ = this.searchTerms.pipe(
      // wait 300ms after each keystroke before considering the term
      debounceTime(300),

      // ignore new term if same as previous term
      distinctUntilChanged(),

      // switch to new search observable each time the term changes
      switchMap((term: string) => this.userService.searchGatePasses(term, this.print, this.project)),

    );
    // this.userFunc();
  }


  selectFunc(gpArray: GpArray) {
    if(this.print === '1') {
      this.printService.generatePdf(gpArray, 'Print')
    }
    else {
    this.userService.selectedGpArray = gpArray;
    this.userService.storeGpArray(gpArray.gatePassNo);
    this.router.navigate(['return']);
    }
  }
}
