import { Injectable } from '@angular/core';
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
import { formatDate } from '@angular/common';
import { MaterialGatePass } from '../../models/gatePass';
import { GpArray } from '../../models/gpArray';
import { User } from '../../models/user';
import { Gate } from '../../models/gate';
import { Project } from '../../models/project';
pdfMake.vfs = pdfFonts.pdfMake.vfs;

@Injectable({
  providedIn: 'root'
})
export class PrintService {

  constructor() { }

  gates: Gate[];
  selectedGateViewValue: String;

  calcSelectedGate(selectedGateId: string, selectedProject: Project) {
    this.gates = selectedProject.gates;
    const gate = this.gates.find((gate) => gate._id === selectedGateId)
    this.selectedGateViewValue = gate.viewValue;
  }

  getGatePassObject(gatePass: MaterialGatePass[], selectedType: String) {
    if (selectedType === 'Returnable') {
      return {
        table: {
          // headerRows: 1,
          widths: ['*', '*', '*', '*', '*', '*', '*', '*', '*', '*', '*'],
          body: [
            [{
              text: 'SNo.',
              style: 'tableHeader'
            },
            {
              text: 'Mode Of Transport',
              style: 'tableHeader'
            },
            {
              text: 'Description',
              style: 'tableHeader'
            },
            {
              text: 'Quantity',
              style: 'tableHeader'
            },
            {
              text: 'Unit',
              style: 'tableHeader'
            },
            {
              text: 'Issued To',
              style: 'tableHeader'
            },
            {
              text: 'From',
              style: 'tableHeader'
            },
            {
              text: 'To',
              style: 'tableHeader'
            },
            {
              text: 'Reason',
              style: 'tableHeader'
            },
            {
              text: 'Remark',
              style: 'tableHeader'
            },
            {
              text: 'Date Of Return',
              style: 'tableHeader'
            },
            ],
            ...gatePass.map(gp => {
              var date = formatDate(gp.dateOfReturn, 'dd-MM-yyyy', 'en-US', '+0530');
              return [gp.sno, gp.modeOfTransport, gp.description, gp.quantity, gp.unit.viewValue, gp.issuedTo, gp.from, gp.to, gp.reason, gp.remark, date];
            })
            //, gp.unit
          ]
        }
      };
    }
    else {
      return {
        table: {
          // headerRows: 1,
          widths: ['*', '*', '*', '*', '*', '*', '*', '*', '*', '*'],
          body: [
            [{
              text: 'SNo.',
              style: 'tableHeader'
            },
            {
              text: 'Mode Of Transport',
              style: 'tableHeader'
            },
            {
              text: 'Description',
              style: 'tableHeader'
            },
            {
              text: 'Quantity',
              style: 'tableHeader'
            },
            {
              text: 'Unit',
              style: 'tableHeader'
            },
            {
              text: 'Issued To',
              style: 'tableHeader'
            },
            {
              text: 'From',
              style: 'tableHeader'
            },
            {
              text: 'To',
              style: 'tableHeader'
            },
            {
              text: 'Reason',
              style: 'tableHeader'
            },
            {
              text: 'Remark',
              style: 'tableHeader'
            },
            ],
            ...gatePass.map(gp => {
              return [gp.sno, gp.modeOfTransport, gp.description, gp.quantity, gp.unit.viewValue, gp.issuedTo, gp.from, gp.to, gp.reason, gp.remark];
            })
            //, gp.unit
          ]
        }
      };
    }
  }

  getAuthorizedByHeaderObject(authorized: User) {
    if (authorized) {
      return {
        text: 'Authorized By: ',
        style: 'header'
      }
    }
  }

  getAuthorizedByObject(authorized: User, authorizedDate: Date) {
    if (authorized) {
      const exs = [];
      var authorizedAtDate = formatDate(authorizedDate, 'dd-MM-yyyy', 'en-US', '+0530');
      var authorizedAtTime = formatDate(authorizedDate, 'hh:mm:ss a', 'en-US', '+0530');
      exs.push(
        [{
          columns: [
            [{
              text: 'Employee id: ' + authorized.empId
            },
            {
              text: 'Name: ' + authorized.name
            },
            {
              text: 'Department: ' + authorized.selectedDepartment.viewValue
            },
            {
              text: 'Grade: ' + authorized.selectedGrade.viewValue
            },
            {
              text: 'Project: ' + authorized.selectedProject.viewValue
            },
            {
              text: 'Date: ' + authorizedAtDate
            },
            {
              text: 'Time: ' + authorizedAtTime
            },
            ]
          ]
        }]
      )

      return {
        table: {
          widths: ['auto'],
          body: [
            ...exs
          ]
        },
      }
    }
  }

  getCreatedByObject(createdBy: User, createdAt: Date) {
    const exs = [];
    var createdAtDate = formatDate(createdAt, 'dd-MM-yyyy', 'en-US', '+0530');
    var createdAtTime = formatDate(createdAt, 'hh:mm:ss a', 'en-US', '+0530');
    exs.push(
      [{
        columns: [
          [{
            text: 'Employee id: ' + createdBy.empId
          },
          {
            text: 'Name: ' + createdBy.name
          },
          {
            text: 'Department: ' + createdBy.selectedDepartment.viewValue
          },
          {
            text: 'Grade: ' + createdBy.selectedGrade.viewValue
          },
          {
            text: 'Project: ' + createdBy.selectedProject.viewValue
          },
          {
            text: 'Date: ' + createdAtDate
          },
          {
            text: 'Time: ' + createdAtTime
          },
          ]
        ]
      }]
    )

    return {
      table: {
        widths: ['auto'],
        body: [
          ...exs
        ]
      }
    }
  }

  getDocumentDefinition(gpArray: GpArray, selectedGateViewValue: String) {

    sessionStorage.setItem('gpArray', JSON.stringify(gpArray));

    return {
      pageOrientation: 'landscape',
      content: [
        {
          text: 'NTPC ' + gpArray.createForm.selectedProject.viewValue + ' Gate Pass System',
          bold: true,
          fontSize: 20,
          alignment: 'center',
          margin: [0, 0, 0, 15]
        },
        {
          text: 'Gate Pass Number ' + gpArray.gatePassNo,
          bold: true,
          fontSize: 16,
          alignment: 'center',
          margin: [0, 0, 0, 10]
        },
        {
          text: 'Status: ' + gpArray.status,
          bold: true,
          fontSize: 12,
          alignment: 'center',
          margin: [0, 0, 0, 20]
        },
        {
          columns: [
            [{
              text: 'Name : ' + gpArray.createForm.name,
              //  style: 'name',
            },
            {
              text: 'Movement : ' + gpArray.createForm.selectedMovement,
            }],
            [
              {
                text: 'Department : ' + gpArray.createForm.selectedDepartment.viewValue,
              },
              {
                text: 'Gate : ' + selectedGateViewValue,
              }
            ],
            [
              {
                text: 'Type : ' + gpArray.createForm.selectedType,
              },
              {
                text: 'Firm Represented : ' + gpArray.createForm.firm,
              },
            ],
            [{
              text: 'Project : ' + gpArray.createForm.selectedProject.viewValue,
            },
            {
              text: 'Receiver : ' + gpArray.createForm.receiver,
            },
            ],
            [
              {
                text: 'Owner : ' + gpArray.createForm.selectedOwner
              },
              {
                text: 'Vendor Name : ' + gpArray.createForm.vendorName
              }
            ],
          ],
          margin: [0, 0, 0, 20],
        },

        this.getGatePassObject(gpArray.gatePass, gpArray.createForm.selectedType),
        // {
        //   pageOrientation: 'landscape',
        // }
        {
          columns: [
            [
              {
                text: 'Created By: ',
                style: 'header'
              },
              this.getCreatedByObject(gpArray.createdBy, gpArray.createdAt),
            ],
            [
              this.getAuthorizedByHeaderObject(gpArray.authorized),

              this.getAuthorizedByObject(gpArray.authorized, gpArray.authorizedDate),
            ]
          ]
        }

      ],
      info: {
        title: 'Gate-Pass-' + gpArray.gatePassNo,
        author: gpArray.createdBy.name,
        subject: 'Gate Pass',
        keywords: 'GATE PASS, MATERIAL GATE PASS',
      },
      styles: {
        name: {
          fontSize: 16,
          bold: true
        },
        tableHeader: {
          bold: true,
        },
        header: {
          fontSize: 14,
          bold: true,
          margin: [0, 20, 0, 10]
        },
        // gatePass__header {
        //   margin: [0, 0, 0, 15]
        // }
      },
    };
  }

  generatePdf(gpArray: GpArray, source: string) {
    this.calcSelectedGate(gpArray.createForm.selectedGate, gpArray.createdBy.selectedProject)
    if (source === 'Print') {
      gpArray.createdBy = this.createdByDetails(gpArray.createdBy, gpArray.createdAt)
      gpArray.authorized = this.authorizedDetails(gpArray.authorized, gpArray.authorizedDate)
    }
    const documentDefinition = this.getDocumentDefinition(gpArray, this.selectedGateViewValue);
    // const documentDefinition = { content: 'This is a test PDF'};
    pdfMake.createPdf(documentDefinition).open();
  }

  authorizedDetails(authorized: User, authorizedDate: Date) {
    const iterator4 = authorized.Project[Symbol.iterator]();
    if (authorized.Project.length > 1) {
      for (const Pro of iterator4) {
        if ((authorizedDate >= Pro.from && authorizedDate < Pro.to) || Pro.to === null) {
          authorized.selectedProject = Pro.selectedProject;
          break;
        }
      }
    }
    const iterator5 = authorized.Grade[Symbol.iterator]();
    if (authorized.Grade.length > 1) {
      for (const Gra of iterator5) {
        if ((authorizedDate >= Gra.from && authorizedDate < Gra.to) || Gra.to === null) {
          authorized.selectedGrade = Gra.selectedGrade;
          break;
        }
      }
    }
    const iterator6 = authorized.Department[Symbol.iterator]();
    if (authorized.Department.length > 1) {
      for (const Dep of iterator6) {
        if ((authorizedDate >= Dep.from && authorizedDate < Dep.to) || Dep.to === null) {
          authorized.selectedDepartment = Dep.selectedDepartment;
          break;
        }
      }
    }
    return authorized;
  }

  createdByDetails(createdBy: User, createdAt: Date) {
    const iterator = createdBy.Project[Symbol.iterator]();
    if (createdBy.Project.length > 1) {
      for (const Pro of iterator) {
        if ((createdAt >= Pro.from && createdAt < Pro.to) || Pro.to === null) {
          createdBy.selectedProject = Pro.selectedProject;
          break;
        }
      }
    }

    const iterator2 = createdBy.Grade[Symbol.iterator]();
    if (createdBy.Grade.length > 1) {
      for (const Gra of iterator2) {
        if ((createdAt >= Gra.from && createdAt < Gra.to) || Gra.to === null) {
          createdBy.selectedGrade = Gra.selectedGrade;
          break;
        }
      }
    }
    const iterator3 = createdBy.Department[Symbol.iterator]();
    if (createdBy.Department.length > 1) {
      for (const Dep of iterator3) {
        if ((createdAt >= Dep.from && createdAt < Dep.to) || Dep.to === null) {
          createdBy.selectedDepartment = Dep.selectedDepartment;
          break;
        }
      }
    }
    return createdBy;
  }

}
