import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { rowsAnimation } from '../animations/template.animations';
import { GpArray } from '../models/gpArray';
import { SearchTable } from '../models/searchTable';
import { Unit } from '../models/unit';
import { User } from '../models/user';
import { UserService } from '../services/user/user.service';

@Component({
  selector: 'app-inbox',
  templateUrl: './inbox.component.html',
  styleUrls: ['./inbox.component.css'],
  animations: [rowsAnimation],
})
export class InboxComponent implements OnInit {

  displayedColumns: string[] = ['id', 'sno', 'description', 'quantity', 'unit', 'dateOfCreation', 'dueDateOfReturn', 'department', 'createdBy', 'owner', 'movement', 'status'];
  dataSource: MatTableDataSource<SearchTable>;

  authDisplayedColumns: string[] = ['id', 'sno', 'description', 'quantity', 'unit', 'dateOfCreation', 'dueDateOfReturn', 'department', 'createdBy', 'owner', 'movement', 'status'];
  authDataSource: MatTableDataSource<SearchTable>;

  searchTableData: SearchTable[] = [];
  authSearchTableData: SearchTable[] = []

  constructor(private userService: UserService, public dialog: MatDialog, private router: Router) { }

  Units: Unit[];

  ngOnInit(): void {
    this.getGpArray();
    this.getAuthGpArray();
  }

  filterData(gpArrays) {
    for (const gp of gpArrays) {
      for (const tableRow of gp.gatePass) {
        if (tableRow.sno == 1) {
          this.searchTableData.push({
            id: gp.gatePassNo,
            sno: tableRow.sno,
            quantity: tableRow.quantity,
            unit: tableRow.unit,
            dateOfCreation: gp.createdAt,
            dueDateOfReturn: tableRow.dateOfReturn,
            department: gp.createForm.selectedDepartment.viewValue,
            createdBy: gp.createdBy.name,
            owner: gp.createForm.selectedOwner,
            movement: gp.createForm.selectedMovement,
            status: gp.status,
            description: tableRow.description
          })
        }
        else {
          this.searchTableData.push({
            vId: gp.gatePassNo,
            sno: tableRow.sno,
            quantity: tableRow.quantity,
            unit: tableRow.unit,
            dateOfCreation: gp.createdAt,
            dueDateOfReturn: tableRow.dateOfReturn,
            department: gp.createForm.selectedDepartment.viewValue,
            createdBy: gp.createdBy.name,
            owner: gp.createForm.selectedOwner,
            movement: gp.createForm.selectedMovement,
            status: gp.status,
            description: tableRow.description
          })
        }
      }
    }
  }

  authFilterData(gpArrays) {
    for (const gp of gpArrays) {
      for (const tableRow of gp.gatePass) {
        if (tableRow.sno == 1) {
          this.authSearchTableData.push({
            id: gp.gatePassNo,
            sno: tableRow.sno,
            quantity: tableRow.quantity,
            unit: tableRow.unit,
            dateOfCreation: gp.createdAt,
            dueDateOfReturn: tableRow.dateOfReturn,
            department: gp.createForm.selectedDepartment.viewValue,
            createdBy: gp.createdBy.name,
            owner: gp.createForm.selectedOwner,
            movement: gp.createForm.selectedMovement,
            status: gp.status,
            description: tableRow.description
          })
        }
        else {
          this.authSearchTableData.push({
            vId: gp.gatePassNo,
            sno: tableRow.sno,
            quantity: tableRow.quantity,
            unit: tableRow.unit,
            dateOfCreation: gp.createdAt,
            dueDateOfReturn: tableRow.dateOfReturn,
            department: gp.createForm.selectedDepartment.viewValue,
            createdBy: gp.createdBy.name,
            owner: gp.createForm.selectedOwner,
            movement: gp.createForm.selectedMovement,
            status: gp.status,
            description: tableRow.description
          })
        }
      }
    }
  }

  rId;
  getRecord(row) {
    row.id == null ? this.rId = row.vId : this.rId = row.id;
    const gp = this.gpArrays.find(gp => gp.gatePassNo == this.rId);
    this.onSelect(gp);
    // for(const gp of this.gpArrays) {
    //   if(gp.gatePassNo === this.rId) {
    //     this.onSelect(gp);
    //     console.log(gp);
    //     break;
    //   }
    // }
  }

  aRId;
  authGetRecord(row) {
    row.id == null ? this.aRId = row.vId : this.aRId = row.id;
    const gp = this.authGpArrays.find(gp => gp.gatePassNo == this.aRId);
    this.onSelect(gp);
    // for(const gp of this.gpArrays) {
    //   if(gp.gatePassNo === this.rId) {
    //     this.onSelect(gp);
    //     console.log(gp);
    //     break;
    //   }
    // }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  authApplyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.authDataSource.filter = filterValue.trim().toLowerCase();
  }

  loggedIn: User = this.userService.loggedIn

  gpArrays: GpArray[];
  authGpArrays: GpArray[];
  // getGpArray() {
  //   console.log(this.userService.gpArrays)
  //   if (this.userService.gpArrays == undefined) {
  //     this.refreshFilter();
  //   }
  //   else {
  //     this.gpArrays = this.userService.gpArrays;
  //     this.filterData(this.gpArrays)
  //     this.dataSource = new MatTableDataSource(this.searchTableData);
  //   }
  // }

  authToId = []
  pendingBool = false;
  authBool = false;

  getGpArray() {
    this.loggedIn.authorizedTo.forEach(authTo => {
      this.authToId.push(authTo._id)
    })
    console.log(this.authToId)
    this.userService.getInboxGpArray(this.authToId)
      .subscribe((gpArrays: GpArray[]) => {
        this.gpArrays = gpArrays
        if(this.gpArrays.length > 0) {
          this.pendingBool = true;
        }
        this.filterData(gpArrays)
        this.dataSource = new MatTableDataSource(this.searchTableData);
      })
  }

  getAuthGpArray() {
    this.userService.getInboxAuthGpArray(this.loggedIn._id)
      .subscribe((authGpArrays: GpArray[]) => {
        console.log(authGpArrays)
        this.authGpArrays = authGpArrays
        if(this.authGpArrays.length > 0) {
          this.authBool = true;
        }
        this.authFilterData(authGpArrays)
        this.authDataSource = new MatTableDataSource(this.authSearchTableData)
      })
  }

  // refreshFilter() {
  //   this.userService.getFilterGpArray(this.userService.getFilter())
  //     .subscribe((gpArrays) => {
  //       console.log(gpArrays)
  //       this.userService.gpArrays = gpArrays;
  //       this.gpArrays = this.userService.gpArrays;
  //       this.filterData(this.gpArrays)
  //       this.dataSource = new MatTableDataSource(this.searchTableData);
  //     })
  // }

  // selectedGpArray: GpArray;
  user: User;
  dcDate;
  onSelect(gpArray: GpArray): void {
    this.userService.selectedGpArray = gpArray;
    this.userService.storeGpArray(gpArray.gatePassNo);
    this.router.navigate(['return']);
  }

  getUnits() {
    this.userService.getUnits()
      .subscribe(units => this.Units = units);
  }


}
