import { Component, OnInit, Input } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { User } from '../models/user';
import {
   debounceTime, distinctUntilChanged, switchMap
 } from 'rxjs/operators';
import { UserService } from '../services/user/user.service';
import { AdminAuthorizationComponent } from '../admin-authorization/admin-authorization.component';

@Component({
  selector: 'app-employee-search',
  templateUrl: './employee-search.component.html',
  styleUrls: ['./employee-search.component.css']
})

export class EmployeeSearchComponent implements OnInit {

  users$: Observable<User[]>;
  private searchTerms = new Subject<string>();
  @Input() selectedAuth: boolean;
  @Input() selectedCreation: boolean;
  @Input() selectedProject: string;
  constructor(private userService: UserService, private adminAuth: AdminAuthorizationComponent) { }

  search(term: string): void {
    this.searchTerms.next(term);
  }

  ngOnInit(): void {
    this.users$ = this.searchTerms.pipe(
      // wait 300ms after each keystroke before considering the term
      debounceTime(300),

      // ignore new term if same as previous term
      distinctUntilChanged(),

      // switch to new search observable each time the term changes
      switchMap((term: string) => this.userService.searchUsers(term, this.selectedAuth, this.selectedProject)),

    );
    // this.userFunc();
  }


  selectFunc(user: User) {
    if(this.selectedAuth) {
      this.adminAuth.selectedAuthUser = user
    }
    else {
      this.adminAuth.selectedUnAuthUser = user
    }
  }
}
