import { Component, OnInit, Input } from '@angular/core';
import { UserService } from '../services/user/user.service';
import { GpArray } from '../models/gpArray';
import { MatTableDataSource } from '@angular/material/table';
import { rowsAnimation } from '../animations/template.animations';
import { MatDialog } from '@angular/material/dialog';
import { User } from '../models/user';
import { SearchTable } from '../models/searchTable';
import { Router } from '@angular/router';
import { CdkColumnDef, CdkTableModule } from '@angular/cdk/table';
import { Unit } from '../models/unit';


@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.css'],
  animations: [rowsAnimation],
  // providers: [CdkColumnDef, CdkTableModule]
})
export class ReportComponent implements OnInit {

  displayedColumns: string[] = ['id', 'sno', 'description', 'quantity', 'unit', 'dateOfCreation', 'dueDateOfReturn', 'department', 'createdBy', 'owner', 'movement', 'status'];
  dataSource: MatTableDataSource<SearchTable>;

  searchTableData: SearchTable[] = [];

  constructor(private userService: UserService, public dialog: MatDialog, private router: Router) {

  }

  Units: Unit[];

  ngOnInit(): void {
    this.getGpArray();
    // this.getUnits();
  }

  filterData(gpArrays) {
    for (const gp of gpArrays) {
      for (const tableRow of gp.gatePass) {
        if (tableRow.sno == 1) {
          this.searchTableData.push({
            id: gp.gatePassNo,
            sno: tableRow.sno,
            quantity: tableRow.quantity,
            unit: tableRow.unit,
            dateOfCreation: gp.createdAt,
            dueDateOfReturn: tableRow.dateOfReturn,
            department: gp.createForm.selectedDepartment.viewValue,
            createdBy: gp.createdBy.name,
            owner: gp.createForm.selectedOwner,
            movement: gp.createForm.selectedMovement,
            status: gp.status,
            description: tableRow.description
          })
        }
        else {
          this.searchTableData.push({
            vId: gp.gatePassNo,
            sno: tableRow.sno,
            quantity: tableRow.quantity,
            unit: tableRow.unit,
            dateOfCreation: gp.createdAt,
            dueDateOfReturn: tableRow.dateOfReturn,
            department: gp.createForm.selectedDepartment.viewValue,
            createdBy: gp.createdBy.name,
            owner: gp.createForm.selectedOwner,
            movement: gp.createForm.selectedMovement,
            status: gp.status,
            description: tableRow.description
          })
        }
      }
    }
  }
  rId;
  getRecord(row) {
    row.id == null ? this.rId = row.vId : this.rId = row.id;
    const gp = this.gpArrays.find(gp => gp.gatePassNo == this.rId);
    this.onSelect(gp);
    // for(const gp of this.gpArrays) {
    //   if(gp.gatePassNo === this.rId) {
    //     this.onSelect(gp);
    //     console.log(gp);
    //     break;
    //   }
    // }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  loggedIn: User = this.userService.loggedIn

  gpArrays: GpArray[];
  getGpArray() {
    console.log(this.userService.gpArrays)
    if (this.userService.gpArrays == undefined) {
      this.refreshFilter();
    }
    else {
      this.gpArrays = this.userService.gpArrays;
      this.filterData(this.gpArrays)
      this.dataSource = new MatTableDataSource(this.searchTableData);
    }
  }

  refreshFilter() {
    this.userService.getFilterGpArray(this.userService.getFilter())
      .subscribe((gpArrays) => {
        console.log(gpArrays)
        this.userService.gpArrays = gpArrays;
        this.gpArrays = this.userService.gpArrays;
        this.filterData(this.gpArrays)
        this.dataSource = new MatTableDataSource(this.searchTableData);
      })
  }

  selectedGpArray: GpArray;
  user: User;
  dcDate;
  onSelect(gpArray: GpArray): void {
    this.userService.selectedGpArray = gpArray;
    this.userService.storeGpArray(gpArray.gatePassNo);
    this.router.navigate(['return']);
  }

  getUnits() {
    this.userService.getUnits()
      .subscribe(units => this.Units = units);
  }

}
