import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { UserService } from '../services/user/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-filter-gate-pass',
  templateUrl: './filter-gate-pass.component.html',
  styleUrls: ['./filter-gate-pass.component.css']
})
export class FilterGatePassComponent implements OnInit {

  filterForm: FormGroup;

  constructor(public userService: UserService, private fb: FormBuilder, private router: Router) {
    this.filterForm = this.fb.group({
      selectedDepartment: new FormControl('', Validators.required),
      selectedType: new FormControl('', Validators.required),
      selectedOwner: new FormControl('', Validators.required),
      selectedMovement: new FormControl('', Validators.required),
      from: new FormControl('', Validators.compose([Validators.required])),
      to: new FormControl('', Validators.required),
      selectedProject: new FormControl(this.userService.loggedIn.selectedProject._id)
    });

   }

  ngOnInit(): void {
  }

  onSubmit(filterForm) {
    if(filterForm.selectedDepartment === 'Own Department') {
    filterForm.selectedDepartment = this.userService.loggedIn.selectedDepartment._id;
    }
    console.log(filterForm);
    this.userService.getFilterGpArray( filterForm )
      .subscribe((gpArrays) => {
        this.userService.gpArrays = gpArrays;
        this.router.navigate(['report']);
      })
  }

}
