import { Attribute, Directive, Input } from '@angular/core';
import { AbstractControl, NG_VALIDATORS, Validator } from '@angular/forms';

@Directive({
  selector: '[appQuantityRetValidator]',
  providers: [{provide: NG_VALIDATORS, useExisting: QuantityRetValidatorDirective, multi: true}]
})
export class QuantityRetValidatorDirective implements Validator {
  @Input('appQuantityRetValidator') str: string;


  // valueInLimit(quantityNum: number, quantityRet: number) {
  //   let tolerance = 50;
  //   let quantityRetTop = quantityNum + quantityNum*(tolerance/100);
  //   let quantityRetBottom = quantityNum - quantityNum*(tolerance/100);
  //   if((quantityRet>quantityRetBottom) && (quantityRet<quantityRetTop)) {
  //     return true;
  //   }
  //   return false;
  // }

  validate(control: AbstractControl): {[key: string]: any} | null {
    const words = this.str.split(' ');
    let quantityStr = words[0]
    let toleranceStr = words[1]

    let quantityNum = Number(quantityStr)
    let toleranceNum = Number(toleranceStr)

    // console.log('Tolerance', toleranceNum)
    // console.log('Quantity', quantityNum)

    let quantityRet = control.value;

    // console.log(quantityNum)
    // let tolerance = 50;
    // console.log(quantityRet, tolerance, quantityNum)
    let quantityRetTop = quantityNum + quantityNum*(toleranceNum/100);
    let quantityRetBottom = quantityNum - quantityNum*(toleranceNum/100);
    // console.log('QuantityRetTop',quantityRetTop, 'QuantityRetBottom',quantityRetBottom)
    // return this.quantity ? quantityValidator(this.valueInLimit(quantityNum, quantityRet))  : null
    return ((quantityRet>=quantityRetBottom) && (quantityRet<=quantityRetTop)) ? null : {invalidQuantity: {value: control.value}}
  }

}
