  loginUser(user: User): Observable<boolean> {
    console.log(user)
    const url = `${this.usersUrl}/login`;
    return this.http.post<any>(url, user)
      .pipe(
        tap(userJwt =>
          // console.log('data: ',tokens)
          this.doLoginUser(userJwt)
          // this.log(`logged in user w/ empId=${data.user.empId}`)
        ),
        mapTo(true),
        catchError(this.handleError<any>('loginUser', []))
    )
  }

  private doLoginUser(userJwt: UserJwt) {
    console.log(userJwt.user)
    // localStorage.setItem(this.loggedIn, userJwt.user)
    this.loggedIn = userJwt.user
    localStorage.setItem('USER', JSON.stringify(this.loggedIn))
    var tokens: Tokens;
    tokens = userJwt
    this.storeTokens(tokens);
    this.snackBarMessage(`Logged in successfully! Welcome ${userJwt.user.name}`)
  }

  private storeTokens(tokens: Tokens) {
    localStorage.setItem(this.JWT_TOKEN, tokens.jwt);
    localStorage.setItem(this.REFRESH_TOKEN, tokens.refreshToken)
  }

  logOutUser() {
    console.log('logOutUser')
    const url = `${this.usersUrl}/logout`;
    return this.http.post<any>(url, {
      'refreshToken': this.getRefreshToken()
    }).pipe(
      tap(() => this.doLogoutUser()),
      mapTo(true),
      catchError(this.handleError<any>('logOutUser'))
    )
  }

  private doLogoutUser() {
    this.loggedIn = null
    this.snackBarMessage('Logged out successfully')
    this.removeTokens();
  }

  private removeTokens() {
    localStorage.removeItem('USER');
    localStorage.removeItem(this.JWT_TOKEN);
    localStorage.removeItem(this.REFRESH_TOKEN);
    localStorage.removeItem('gatePassNo');
    localStorage.removeItem('filter');
  }

  refreshToken() {
    const url = `${this.usersUrl}/refresh`
    return this.http.post<any>(url, {
      'refreshToken': this.getRefreshToken()
    }).pipe(tap((tokens: Tokens) => {
      this.storeJwtToken(tokens.jwt);
    }));
  }

  private getRefreshToken() {
    return localStorage.getItem(this.REFRESH_TOKEN)
  }

  private storeJwtToken(jwt: string) {
    localStorage.setItem(this.JWT_TOKEN, jwt)
  }

  getJwtToken() {
    return localStorage.getItem(this.JWT_TOKEN);
  }

  searchUsers(term: string, selectedAuthorization: boolean, selectedProject: string): Observable<User[]> {
    if (!term.trim()) {
      // if not search term, return empty hero array.
      return of([]);
    }
    return this.http.get<User[]>(`${this.usersUrl}/user/search/${term}/${selectedAuthorization}/${selectedProject}`).pipe(
      tap(x => x.length ?
         this.log(`found users matching "${term}"`) :
         this.log(`no users matching "${term}"`)),
      catchError(this.handleError<User[]>('searchUsers', []))
    );
  }

  searchGatePasses(term: string, print: string, project: string): Observable<GpArray[]> {
    if (!term.trim()) {
      return of([]);
    }
    return this.http.get<GpArray[]>(`${this.usersUrl}/gatepass/search/${term}/${print}/${project}`).pipe(
      tap(x => x.length ?
         this.log(`found gate passes matching "${term}"`) :
         this.log(`no gate passes matching "${term}"`)),
      catchError(this.handleError<GpArray[]>('searchGatePasses', []))
    );
  }
  private log(message: string) {
    this.messageService.add(`UserService: ${message}`);
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error);
      this.log(`${operation} failed: ${error.message}`);
      this.snackBarError(error);
      return of(result as T);
    }
  }


  snackBarMessage(message: string) {

    this._snackBar.open(`${message}`, 'Ok', {
      duration: 2000,
    });

  }

  snackBarError(error) {

    this._snackBar.open(`${error.statusText}`, 'Ok', {
      duration: 2000,
    });

  }

