import { Component, Inject } from '@angular/core';
import { UserService } from './services/user/user.service';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';

export interface DialogData {
  project: string;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Gate Pass';
  constructor(public userService: UserService, public dialog: MatDialog, private router: Router) {
  }
  // loggedIn = this.userService.loggedIn;
  logOut() {
    console.log('logOut')
    this.userService.logOutUser()
      .subscribe(() => this.router.navigate(['login']))
  }

  print() {
    const dialogRef = this.dialog.open(DialogContentPrint, {
      data: {
        project: this.userService.loggedIn.selectedProject._id
      }
    });
    dialogRef.afterClosed().subscribe(result => {
      // console.log(`Dialog result: ${result}`);

    });

  }
}

@Component({
  selector: 'dialog-content-print',
  templateUrl: 'dialog-content-print.html',
})
export class DialogContentPrint {
  constructor(@Inject (MAT_DIALOG_DATA) public data: DialogData) { }
}

@Component({
  selector: 'snack-bar-component-message',
  templateUrl: 'snack-bar-component-message.html',
  styles: [`
    .example-pizza-party {
      color: hotpink;
    }
  `],
})
export class MessageSnackComponent { }
