import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { HomeComponent } from './home/home.component';
import { AdminDataControlComponent } from './admin-data-control/admin-data-control.component';
import { GatePassComponent } from './gate-pass/gate-pass.component';
import { ReportComponent } from './report/report.component';
import { AdminAuthorizationComponent } from './admin-authorization/admin-authorization.component';
import { FilterGatePassComponent } from './filter-gate-pass/filter-gate-pass.component';
import { ReturnComponent } from './return/return.component';
import { ProfileComponent } from './profile/profile.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { AuthGuard } from './guards/auth.guard';
import { LoginGuard } from './guards/login.guard';
import { RoleGuard } from './guards/role.guard';
import { CreateGuard } from './guards/create.guard';
import { SuperAdminGuard } from './guards/super-admin.guard';
import { AuthorizeGuard } from './guards/authorize.guard'
import { InboxComponent } from './inbox/inbox.component';

const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent, canActivate: [LoginGuard] },
  { path: 'register', component: RegisterComponent, canActivate: [LoginGuard] },
  { path: 'home', component: HomeComponent, canActivate: [AuthGuard] },
  { path: 'gatepass', component: GatePassComponent, canActivate: [AuthGuard, CreateGuard] },
  { path: 'admin/data-control', component: AdminDataControlComponent, canActivate: [AuthGuard, SuperAdminGuard] },
  { path: 'admin/authorization', component: AdminAuthorizationComponent, canActivate: [AuthGuard, RoleGuard] },
  { path: 'report', component: ReportComponent, canActivate: [AuthGuard] },
  { path: 'filter', component: FilterGatePassComponent, canActivate: [AuthGuard] },
  { path: 'return', component: ReturnComponent, canActivate: [AuthGuard] },
  { path: 'profile', component: ProfileComponent, canActivate: [AuthGuard] },
  { path: 'inbox', component: InboxComponent, canActivate: [AuthGuard, AuthorizeGuard] },
  { path: '**', component: PageNotFoundComponent, canActivate: [AuthGuard] }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule {}
