import { Component, OnInit, Inject } from '@angular/core';
import { GpArray } from '../models/gpArray';
import { Department } from '../models/department';
import { MatTableDataSource } from '@angular/material/table';
import { MaterialGatePass } from '../models/gatePass';
import { UserService } from '../services/user/user.service';
import { PrintService } from '../services/print/print.service';
import { MatDialog } from '@angular/material/dialog';
import { SelectionModel } from '@angular/cdk/collections';
import { User } from '../models/user';
import { rowsAnimation } from '../animations/template.animations';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Gate } from '../models/gate';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Unit } from '../models/unit';
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
import { formatDate } from '@angular/common';
import { AbstractControl, ValidatorFn } from '@angular/forms';
import { Project } from '../models/project';
pdfMake.vfs = pdfFonts.pdfMake.vfs;

interface Type {
  viewValue: string;
}
interface Owner {
  viewValue: string;
}
interface Movement {
  viewValue: string;
}
// interface Gate {
//   viewValue: string;
// }

@Component({
  selector: 'app-return',
  templateUrl: './return.component.html',
  styleUrls: ['./return.component.css'],
  animations: [rowsAnimation]
})
export class ReturnComponent implements OnInit {

  dcGatePass = ['sno', 'modeOfTransport', 'description', 'quantity', 'unit', 'issuedTo', 'from', 'to', 'reason', 'remark', 'dateOfReturn'];
  dcGatePassStd = ['sno', 'modeOfTransport', 'description', 'quantity', 'unit', 'issuedTo', 'from', 'to', 'reason', 'remark', 'dateOfReturn'];
  dcGatePassRet = ['select', 'sno', 'modeOfTransport', 'description', 'quantity', 'quantityRet', 'unit', 'issuedTo', 'from', 'to', 'reason', 'remark', 'incomingRef', 'dateOfReturn',];
  dcGatePassRetNoCheck = ['sno', 'modeOfTransport', 'description', 'quantity', 'quantityRet', 'unit', 'issuedTo', 'from', 'to', 'reason', 'remark', 'incomingRef', 'dateOfReturn',];
  dsGatePass: MatTableDataSource<MaterialGatePass>;

  disableTextbox = true;
  loggedIn: User = this.userService.loggedIn
  selectedGpArray: GpArray;
  user: User;
  returnState = false;
  dcDate;

  types: Type[] = [
    { viewValue: 'Returnable' },
    { viewValue: 'Non-Returnable' },
  ];
  owners: Owner[] = [
    { viewValue: 'NTPC' },
    { viewValue: 'Vendor' },
  ];
  movements: Movement[] = [
    { viewValue: 'In' },
    { viewValue: 'Out' },
    { viewValue: 'Within' },
  ];
  gates: Gate[];
  Units: Unit[];
  departments: Department[];
  projects: Project[];

  constructor(private userService: UserService, public dialog: MatDialog, private router: Router, private _snackBar: MatSnackBar, private printService : PrintService) { }

  ngOnInit(): void {
    this.getDepartments();
    this.getProjects();
    if(this.userService.selectedGpArray === undefined) {
      this.setSelectedGpArray();
    }
    else {
    this.onSelect(this.userService.selectedGpArray);
    this.gates = this.selectedGpArray.createForm.selectedProject.gates;
    this.getUnits();
    }
  }

  setSelectedGpArray() {
    this.userService.searchGatePasses(this.userService.getGpNoFromLocal(), '0', this.userService.loggedIn.selectedProject._id)
      .subscribe((gpArrays) => {
        this.userService.selectedGpArray = gpArrays[0];
        this.onSelect( this.userService.selectedGpArray )
        this.gates = this.selectedGpArray.createForm.selectedProject.gates;
        this.getUnits();
      })
  }

  // selectedGateViewValue;
  // calcSelectedGate() {
  //   for(const gate of this.gates) {
  //     if(this.selectedGpArray.createForm.selectedGate === gate._id) {
  //       this.selectedGateViewValue = gate.viewValue;
  //       break;
  //     }
  //   }
  // }

  dateFunc(value) {
    console.log(value)
    // this.createForm.controls.selectedType.value
    for (const dc of this.dcGatePass) {
      if (dc == "dateOfReturn") {
        this.dcDate = true;
        break;
      }
      else {
        this.dcDate = false;
      }
    }
    if (value === "Non-Returnable" && this.dcDate === true) {
      this.dcGatePass.pop();
      this.selectedGpArray.gatePass.forEach(gatePass => {
        gatePass.dateOfReturn = null;
      })
    }
    else if (value === "Returnable" && this.dcDate === false) {
      this.dcGatePass.push('dateOfReturn');
    }

  }

  exitBool = false;
  checkExit() {
    if (this.loggedIn.selectedUserType === 'Security' && this.selectedGpArray.status === 'Authorized') {
      this.exitBool = true;
    }
    else {
      this.exitBool = false;
    }
  }

  deleteBool = false;
  checkDelete() {
    if (this.userService.loggedIn.empId === this.selectedGpArray.createdBy.empId && this.selectedGpArray.status === 'Created') {
      this.deleteBool = true;
    }
    else {
      this.deleteBool = false;
    }
  }

  deleteGatePass() {

    const dialogRef = this.dialog.open(DialogContentReturn, {
      data: { header: 'Delete Gate Pass', message: 'Are you sure you want to delete Gate Pass?' }
    });
    dialogRef.afterClosed().subscribe(result => {
      // console.log(`Dialog result: ${result}`);
      if (result == true) {
        this.userService.deleteGpArray(this.selectedGpArray)
        .subscribe((gpArray) => {
          this._snackBar.open(`Gate Pass ${gpArray.gatePassNo} Deleted Successfully`, 'Ok', {
            duration: 2000,
          });
          this.router.navigate(['home'])
        })
      }

    });

  }

  getUnits() {
    this.userService.getUnits()
      .subscribe(units => this.Units = units);
  }

  getProjects() {
    this.userService.getProjects()
      .subscribe(projects => this.projects = projects)
  }

  getDepartments() {
    this.userService.getDepartments()
      .subscribe(departments => this.departments = departments);
  }

  selection = new SelectionModel<MaterialGatePass>(true, []);
  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dsGatePass.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dsGatePass.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: MaterialGatePass): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.sno + 1}`;
  }

  createNewGatePass(sno: number): MaterialGatePass {
    return {
      sno: sno,
      modeOfTransport: null, description: null, quantity: null, unit: {_id: null, tolerance: null, viewValue: null},
      issuedTo: null, dateOfReturn: null, from: null,
      to: null, reason: null, remark: null, incomingRef: null
    };
  }

  // appQuantityRetValidator(quantity: number, tolerance: number): ValidatorFn {
  //   return (control: AbstractControl): { [key: string]: boolean} | null
  //   return null;
  // }

  addRow() {
    this.dsGatePass.data.push(this.createNewGatePass(this.dsGatePass.data.length + 1));
    this.dsGatePass.filter = "";
  }

  deleteRow() {
    this.dsGatePass.data.pop();
    this.dsGatePass.filter = "";
  }

  toggleEdit() {
    this.disableTextbox = false;
  }


  toggleReturn() {
    this.dcGatePass = this.dcGatePassRet;
    this.returnState = true;
  }

  exitGp() {
    if (this.selectedGpArray.status === 'Authorized') {
      this.selectedGpArray.status = 'Exited'
      this.updateData('Are you sure you want to exit Gate Pass?')
    }
    else {
      this._snackBar.openFromComponent(ReturnSnackComponent, {
        duration: 3 * 1000,
      });
    }
  }

  cancel() {
    this.userService.searchGatePasses(this.selectedGpArray.gatePassNo.toString(), '0', this.userService.loggedIn.selectedProject._id)
      .subscribe((gpArray) => {
        this.onSelect(gpArray[0])
      })
    this.returnState = false;
    // this.disableTextbox = true;
    // this.dcGatePass = this.dcGatePassStd;
  }


  onSelect(gpArray: GpArray): void {
    console.log(gpArray)
    this.disableTextbox = true;
    gpArray.createdBy = this.createdByDetails(gpArray.createdBy, gpArray.createdAt)
    if (gpArray.authorized != null) {
      gpArray.authorized = this.authorizedDetails(gpArray.authorized, gpArray.authorizedDate)
    }
    this.selectedGpArray = gpArray;
    this.user = this.selectedGpArray.authorized;
    // if (this.dcGatePass != this.dcGatePassStd) {
    //   this.dcGatePass = this.dcGatePassStd;
    // }

    this.gpStatusCheck();
    this.gatePassTypeCheck();
    this.dsGatePass = new MatTableDataSource(this.selectedGpArray.gatePass);
    this.checkAuth();
    this.checkEdit();
    this.checkExit();
    this.checkReturn();
    this.checkDelete();
  }

  gpStatusCheck() {
    if ((this.selectedGpArray.status === 'Returned' || this.selectedGpArray.status === 'Partially Returned') && this.returnState == false && this.selectedGpArray.createForm.selectedType === 'Returnable') {
      this.dcGatePass = this.dcGatePassRetNoCheck;
    }
    else if (this.selectedGpArray.createForm.selectedType === 'Returnable' && this.returnState == true) {
      this.dcGatePass = this.dcGatePassRet;
    }
    else {
      this.dcGatePass = this.dcGatePassStd;
    }
  }

  gatePassTypeCheck() {
    for (const dc of this.dcGatePass) {
      if (dc == "dateOfReturn") {
        this.dcDate = true;
        break;
      }
      else {
        this.dcDate = false;
      }
    }
    if (this.selectedGpArray.createForm.selectedType === "Non-Returnable" && this.dcDate === true) {
      this.dcGatePass.pop();
    }
    else if (this.selectedGpArray.createForm.selectedType === "Returnable" && this.dcDate === false) {
      this.dcGatePass.push('dateOfReturn');
    }
  }

  authorizedDetails(authorized: User, authorizedDate: Date) {
    const iterator4 = authorized.Project[Symbol.iterator]();
    if (authorized.Project.length > 1) {
      for (const Pro of iterator4) {
        if ((authorizedDate >= Pro.from && authorizedDate < Pro.to) || Pro.to === null) {
          authorized.selectedProject = Pro.selectedProject;
          break;
        }
      }
    }
    const iterator5 = authorized.Grade[Symbol.iterator]();
    if (authorized.Grade.length > 1) {
      for (const Gra of iterator5) {
        if ((authorizedDate >= Gra.from && authorizedDate < Gra.to) || Gra.to === null) {
          authorized.selectedGrade = Gra.selectedGrade;
          break;
        }
      }
    }
    const iterator6 = authorized.Department[Symbol.iterator]();
    if (authorized.Department.length > 1) {
      for (const Dep of iterator6) {
        if ((authorizedDate >= Dep.from && authorizedDate < Dep.to) || Dep.to === null) {
          authorized.selectedDepartment = Dep.selectedDepartment;
          break;
        }
      }
    }
    return authorized;
  }

  createdByDetails(createdBy: User, createdAt: Date) {
    const iterator = createdBy.Project[Symbol.iterator]();
    if (createdBy.Project.length > 1) {
      for (const Pro of iterator) {
        if ((createdAt >= Pro.from && createdAt < Pro.to) || Pro.to === null) {
          createdBy.selectedProject = Pro.selectedProject;
          break;
        }
      }
    }

    const iterator2 = createdBy.Grade[Symbol.iterator]();
    if (createdBy.Grade.length > 1) {
      for (const Gra of iterator2) {
        if ((createdAt >= Gra.from && createdAt < Gra.to) || Gra.to === null) {
          createdBy.selectedGrade = Gra.selectedGrade;
          break;
        }
      }
    }
    const iterator3 = createdBy.Department[Symbol.iterator]();
    if (createdBy.Department.length > 1) {
      for (const Dep of iterator3) {
        if ((createdAt >= Dep.from && createdAt < Dep.to) || Dep.to === null) {
          createdBy.selectedDepartment = Dep.selectedDepartment;
          break;
        }
      }
    }
    return createdBy;
  }

  editBool;
  checkEdit() {
    if (this.userService.loggedIn.empId === this.selectedGpArray.createdBy.empId && this.selectedGpArray.status === 'Created') {
      this.editBool = true;
    }
    else {
      this.editBool = false;
    }
  }

  returnBool = false;
  checkReturn() {
    if((this.userService.loggedIn.selectedUserType === 'Security') && (this.selectedGpArray.status === 'Exited' || this.selectedGpArray.status === 'Partially Returned')) {
      this.returnBool = true;
    }
  }

  authBool;
  checkAuth() {
    if (!this.userService.loggedIn.selectedAuthorization || this.selectedGpArray.authorized) {
      this.authBool = false;
      return;
    }
    else {
      const iterator = this.userService.loggedIn.authorizedTo[Symbol.iterator]();
      for (const authorizedToo of iterator) {
        if (authorizedToo.empId === this.selectedGpArray.createdBy.empId) {
          this.authBool = true;
        }
      }
    }
  }

  selectedGpArray2: GpArray;
  toggleAuthorize() {
    this.selectedGpArray2 = this.selectedGpArray;
    this.selectedGpArray2.authorized = this.loggedIn;
    this.selectedGpArray2.status = 'Authorized';
    // console.log(this.selectedGpArray.authorized)
    // console.log(this.selectedGpArray)
    this.userService.updateGpArray(this.selectedGpArray2 as GpArray)
      .subscribe((updatedGpArray) => {
        this.onSelect(updatedGpArray)
        // this.user = updatedGpArray.authorized;
        // this.selectedGpArray.authorizedDate = updatedGpArray.authorizedDate;
        // this.authBool = false;
        // location.reload();
      })
  }

  // checkStatus() {
  //   var counter = 0;
  //   for (let i=0; i<this.selectedGpArray.gatePass.length; i++) {
  //     if(this.selectedGpArray.gatePass[i].quantityRet > )
  //   }
  // }

  updateData(msg?: string) {
    const dialogRef = this.dialog.open(DialogContentReturn, {
      data: { header: 'Update Data', message: msg }
    });
    dialogRef.afterClosed().subscribe(result => {
      // console.log(`Dialog result: ${result}`);
      if (result == true) {
        if (this.returnState === true) {
          this.selectedGpArray.status === 'Returned'
        }
        this.userService.updateGpArray(this.selectedGpArray as GpArray)
          .subscribe((gpArray) => {
            // location.reload();
            this.onSelect(gpArray);
          })
      }
      else {
        if(msg === 'Are you sure you want to exit Gate Pass?') {
          this.selectedGpArray.status = 'Authorized';
        }
      }
    });

  }

  generatePdf() {
    this.printService.generatePdf(this.selectedGpArray, 'Return')
  }


}

