  addAuthorization(authId: string, uId: User) {
    console.log(this.selectedAuthUser.authorizedTo)
    const iterator = this.selectedAuthUser.authorizedTo[Symbol.iterator]();
    for (const authorizedToo of iterator) {
      if(authorizedToo.empId === uId.empId) {
        this._snackBar.openFromComponent(AlreadyAuthComponent, {
          duration: 3 * 1000,
        });
        // alert("You have already added authorization for this user")
        return;
      }
    }

    this.userService.addAuthorization(authId, uId)
      .subscribe((authUser) => {
        // this.location.reload()
        console.log(authUser)
        this.selectedAuthUser = authUser
      })
  }



