import { Component, OnInit } from '@angular/core';
import { rowsAnimation } from '../animations/template.animations';
import { MatTableDataSource } from '@angular/material/table';
import { ProjectArray } from '../models/projectArray';
import { GradeArray } from '../models/gradeArray';
import { DepartmentArray } from '../models/departmentArray';
import { User } from '../models/user';
import { UserService } from '../services/user/user.service';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Project } from '../models/project';
import { Grade } from '../models/grade';
import { Department } from '../models/department';


interface Authorization {
  value: boolean;
  viewValue: string;
}
interface Creation {
  value: boolean;
  viewValue: string;
}
interface UserType {
  viewValue: string;
}

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css'],
  animations: [rowsAnimation]
})
export class ProfileComponent implements OnInit {


  dcUserAuthTo = ['empId', 'name', 'department', 'grade', 'project', 'authorization', 'creation'];
  dsUserAuthTo: MatTableDataSource<User>;

  dcUserHistory = ['department', 'from', 'to'];
  dsUserHistory: MatTableDataSource<DepartmentArray>

  dcUserHistoryGrade = ['grade', 'from', 'to'];
  dsUserHistoryGrade: MatTableDataSource<GradeArray>

  dcUserHistoryProject = ['project', 'from', 'to'];
  dsUserHistoryProject: MatTableDataSource<ProjectArray>

  constructor(private userService: UserService, private router: Router, public dialog: MatDialog, private _snackBar: MatSnackBar) {
    this.dsUserAuthTo = new MatTableDataSource(this.user.authorizedTo)
    this.dsUserHistory = new MatTableDataSource(this.user.Department)
    this.dsUserHistoryGrade = new MatTableDataSource(this.user.Grade)
    this.dsUserHistoryProject = new MatTableDataSource(this.user.Project)
  }

  user = this.userService.loggedIn;

  ngOnInit(): void {
    this.redirectFunc()
    this.getProjects();
    this.getGrades();
    this.getDepartments();
    this.checkUserType();
  }

  showBool = true;
  checkUserType() {
    if(this.user.selectedUserType === 'Security') {
      this.showBool = false;
    }
  }

  projects: Project[];
  grades: Grade[];
  departments: Department[];
  userTypes: UserType[] = [
    { viewValue: 'Super Admin' },
    { viewValue: 'Admin' },
    { viewValue: 'User' },
  ];
  authorizations: Authorization[] = [
    { value: true, viewValue: 'Yes' },
    { value: false, viewValue: 'No' },
  ];
  creations: Creation[] = [
    { value: true, viewValue: 'Yes' },
    { value: false, viewValue: 'No' },
  ];

  getProjects() {
    this.userService.getProjects()
      .subscribe(projects => this.projects = projects);
  }

  getGrades() {
    this.userService.getGrades()
      .subscribe(grades => this.grades = grades);
  }

  getDepartments() {
    this.userService.getDepartments()
      .subscribe(departments => this.departments = departments);
  }

  redirectFunc() {
    if (this.user === null || this.user === undefined) {
      this.router.navigate(['login']);
    }
  }

  cancel() {
    this.userService.getUser(this.user._id)
      .subscribe((user) => this.user = user)
    this.disableTextbox = !this.disableTextbox;
  }

  disableTextbox = true;
  toggleEdit() {
    this.disableTextbox = !this.disableTextbox;
  }


  updateUser() {
    const dialogRef = this.dialog.open(DialogContentHome);

    dialogRef.afterClosed().subscribe(result => {
      // console.log(`Dialog result: ${result}`);
      if (result == true) {
        this.userService.updateUser(this.user as User)
          .subscribe((updatedUser) => {
            // this.user = this.userService.loggedIn
            if (this.user.Department != updatedUser.Department) {
              this.dsUserHistory = new MatTableDataSource(updatedUser.Department)
            }
            if (this.user.Grade != updatedUser.Grade) {
              this.dsUserHistoryGrade = new MatTableDataSource(updatedUser.Grade)
            }
            if (this.user.Project != updatedUser.Project) {
              this.dsUserHistoryProject = new MatTableDataSource(updatedUser.Project)
            }
            this.userService.loggedIn = updatedUser
            this.disableTextbox = true;
            this._snackBar.openFromComponent(UserEditedComponent, {
              duration: 3 * 1000,
            });
          });
      }
      else {
        this.cancel();
      }

    });
  }
}

@Component({
  selector: 'dialog-content-home',
  templateUrl: 'dialog-content-home.html',
})
export class DialogContentHome { }

@Component({
  selector: 'snack-bar-component-userEdited',
  templateUrl: 'snack-bar-component-userEdited.html',
  styles: [`
    .user-edited {
      color: hotpink;
    }
  `],
})
export class UserEditedComponent { }
