import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { UserService } from '../services/user/user.service';
import { Item } from '../models/item';
import { Router } from '@angular/router';
import { ProjectArray } from '../models/projectArray';
import { Project } from '../models/project';

// import { Project } from '../project';
// import { Grade } from '../grade';
// import { Department } from '../department';

@Component({
  selector: 'app-admin-data-control',
  templateUrl: './admin-data-control.component.html',
  styleUrls: ['./admin-data-control.component.css']
})
export class AdminDataControlComponent implements OnInit {

  editForm;
  addForm;
  deleteForm;

  constructor(private fb: FormBuilder, private userService: UserService, private router: Router) {
    // this.editForm = this.fb.group({
    //   selectedItem: new FormControl('', Validators.required),
    //   update: new FormControl('', Validators.required),
    // });
    // this.addForm = this.fb.group({
    //   viewValue: new FormControl('', Validators.required),
    // });
    this.deleteForm = this.fb.group({
      _id: new FormControl('', Validators.required)
    });
  }

  ngOnInit(): void {
  }

  selectedField: string;
  selectedField2: string;
  selectedField3: string;
  fields: string[] = ['Departments', 'Grades', 'Projects', 'Gates', 'Units'];

  // selectedItem: string;
  items: Item[];
  items2: Project[];
  itemsAdd: Item[];
  itemsDelete: Item[];

  onChange() {
    if (this.selectedField == this.fields[0]) {
      this.editForm = this.fb.group({
        selectedItem: new FormControl('', Validators.required),
        update: new FormControl('', Validators.required),
      });
      this.userService.getDepartments()
        .subscribe(departments => this.items = departments);
    }
    else if (this.selectedField == this.fields[1]) {
      this.editForm = this.fb.group({
        selectedItem: new FormControl('', Validators.required),
        update: new FormControl('', Validators.required),
      });
      this.userService.getGrades()
        .subscribe(grades => this.items = grades);
    }
    else if (this.selectedField == this.fields[2]) {
      this.editForm = this.fb.group({
        selectedItem: new FormControl('', Validators.required),
        update: new FormControl('', Validators.required),
      });
      this.userService.getProjects()
        .subscribe(projects => this.items = projects);
    }
    else if (this.selectedField == this.fields[3]) {
      this.editForm = this.fb.group({
        selectedItem2: new FormControl('', Validators.required),
        selectedItem: new FormControl('', Validators.required),
        update: new FormControl('', Validators.required),
      });
      this.userService.getProjects()
        .subscribe(projects => this.items2 = projects);
    }
    else if (this.selectedField == this.fields[4]) {
      this.editForm = this.fb.group({
        selectedItem: new FormControl('', Validators.required),
        update: new FormControl('', Validators.required),
        tolerance: new FormControl('', Validators.required),
      });
      this.userService.getUnits()
        .subscribe(units => this.items = units);
    }
  }

  toleranceUpdate(unitId: string) {
    if(this.selectedField === this.fields[4]) {
      const unit = this.items.find(item => item._id == unitId);
      this.editForm.patchValue({
        tolerance: unit.tolerance
      });
    }
  }

  gateFunc(projectId: string) {
    const project = this.items2.find(item => item._id == projectId);
    this.items = project.gates;
  }

  onChange2() {
    if (this.selectedField2 == this.fields[3]) {
      this.addForm = this.fb.group({
        selectedItem: new FormControl('', Validators.required),
        viewValue: new FormControl('', Validators.required),
      });
      this.userService.getProjects()
        .subscribe(projects => this.itemsAdd = projects);
    }
    else if (this.selectedField2 == this.fields[4]) {
      this.addForm = this.fb.group({
        tolerance: new FormControl('', Validators.required),
        viewValue: new FormControl('', Validators.required),
      });
      // this.addForm.addControl('tolerance', this.fb.control('', [Validators.required]));
    }
    else {
      this.addForm = this.fb.group({
        viewValue: new FormControl('', Validators.required),
      });
    }
  }

  onChange3() {
    if (this.selectedField3 == this.fields[0]) {
      this.userService.getDepartments()
        .subscribe(departments => this.itemsDelete = departments);
    }
    else if (this.selectedField3 == this.fields[1]) {
      this.userService.getGrades()
        .subscribe(grades => this.itemsDelete = grades);
    }
    else if (this.selectedField3 == this.fields[2]) {
      this.userService.getProjects()
        .subscribe(projects => this.itemsDelete = projects);
    }
  }


  onSubmit(editData) {
    console.log(editData)
    if (!editData) { return; }
    else if (editData.selectedItem === '' || editData.selectedItem === null) { return; }
    else if (this.selectedField == this.fields[0]) {
      this.userService.updateDepartment(editData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Department updated: ${Item.viewValue}`)
          location.reload()
        });
    }
    else if (this.selectedField == this.fields[1]) {
      this.userService.updateGrade(editData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Grade updated: ${Item.viewValue}`)
          location.reload()
        });
    }
    else if (this.selectedField == this.fields[2]) {
      this.userService.updateProject(editData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Project updated: ${Item.viewValue}`)
          location.reload()
        });
    }
    else if (this.selectedField == this.fields[3]) {
      this.userService.updateGate(editData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Gate updated: ${Item.viewValue}`)
          location.reload()
        });
    }
    else {
      this.userService.updateUnit(editData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Unit updated: ${Item.viewValue}`)
          location.reload()
          // this.router.routeReuseStrategy.shouldReuseRoute = function () {
          //   return false;
          // };
        });
    }
  }

  onSubmit2(addData) {
    if (!addData) {
      return;
    }
    else if (this.selectedField2 == this.fields[0]) {
      this.userService.addDepartment(addData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Department added: ${Item.viewValue}`)
          location.reload()
        });
    }
    else if (this.selectedField2 == this.fields[1]) {
      this.userService.addGrade(addData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Grade added: ${Item.viewValue}`)
          location.reload()
        });
    }
    else if (this.selectedField2 == this.fields[2]) {
      this.userService.addProject(addData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Project added: ${Item.viewValue}`)
          location.reload()
        });
    }
    else if (this.selectedField2 == this.fields[4]) {
      this.userService.addUnit(addData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Unit added: ${Item.viewValue}`)
          location.reload()
        });
    }
    else {
      this.userService.addGate(addData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Gate added: ${Item.viewValue}`)
          location.reload()
        });
    }
  }

  onSubmit3(deleteData) {
    if (!deleteData) {
      return;
    }
    else if (this.selectedField3 == this.fields[0]) {
      this.userService.deleteDepartment(deleteData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Department deleted: ${Item.viewValue}`)
          location.reload()
        });
    }
    else if (this.selectedField3 == this.fields[1]) {
      this.userService.deleteGrade(deleteData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Grade deleted: ${Item.viewValue}`)
          location.reload()
        });
    }
    else if (this.selectedField3 == this.fields[2]) {
      this.userService.deleteProject(deleteData as Item)
        .subscribe((Item) => {
          this.userService.snackBarMessage(`Project deleted: ${Item.viewValue}`)
          location.reload()
        });
    }
  }

}
