import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { UserService } from '../services/user/user.service';
import { User } from '../models/user';
import { Project } from '../models/project';
import { Grade } from '../models/grade';
import { Department } from '../models/department';
import { ProjectArray } from '../models/projectArray';

interface Authorization {
  value: boolean;
  viewValue: string;
}
interface Creation {
  value: boolean;
  viewValue: string;
}
interface UserType {
  viewValue: string;
}

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  registerForm;

  constructor(private fb: FormBuilder, private userService: UserService) {
    this.registerForm = this.fb.group({
      name: new FormControl('', Validators.required),
      empId: new FormControl('', Validators.compose([Validators.required, Validators.maxLength(6), Validators.minLength(6)])),
      password: new FormControl('', Validators.required),
      selectedProject: new FormControl('', Validators.required),
      selectedGrade: new FormControl('', Validators.required),
      selectedDepartment: new FormControl('', Validators.required),
      selectedUserType: new FormControl('', Validators.required),
      selectedAuthorization: new FormControl('', Validators.required),
      selectedCreation: new FormControl('', Validators.required),
       });
   }

  ngOnInit(): void {
    this.getUsers();
    this.getProjects();
    this.getGrades();
    this.getDepartments();
    }

  selectedProject: string;
  selectedGrade: string;
  selectedDepartment: string;
  selectedUserType: string;
  selectedAuthorization: boolean;
  selectedCreation: boolean;

  users: User[];
  projects: Project[];
  grades: Grade[];
  departments: Department[];
  userTypes: UserType[] = [
    {viewValue: 'Super Admin'},
    {viewValue: 'Admin'},
    {viewValue: 'User'},
    {viewValue: 'Security'}
  ];
  authorizations: Authorization[] = [
    {value: true, viewValue: 'Yes'},
    {value: false, viewValue: 'No'},
  ];
  creations: Creation[] = [
    {value: true, viewValue: 'Yes'},
    {value: false, viewValue: 'No'},
  ];

  securityType = false;
  userTypeFunc(userType) {
    if(userType === this.userTypes[3].viewValue && this.registerForm.contains('selectedGrade')) {
      this.securityType = true;
      this.registerForm.removeControl('selectedGrade')
      this.registerForm.setControl('empId', this.fb.control('', [Validators.required, Validators.maxLength(7), Validators.minLength(7)]));
      this.registerForm.removeControl('selectedDepartment')
      this.registerForm.removeControl('selectedAuthorization')
      this.registerForm.removeControl('selectedCreation')
    }
    else {
      this.securityType = false;
      this.registerForm.addControl('selectedGrade', this.fb.control('', [Validators.required]));
      this.registerForm.setControl('empId', this.fb.control('', [Validators.required, Validators.maxLength(6), Validators.minLength(6)]));
      this.registerForm.addControl('selectedDepartment', this.fb.control('', [Validators.required]));
      this.registerForm.addControl('selectedAuthorization', this.fb.control('', [Validators.required]));
      this.registerForm.addControl('selectedCreation', this.fb.control('', [Validators.required]));
    }
    console.log(this.securityType)
  }

  getProjects() {
    this.userService.getProjects()
        .subscribe(projects => this.projects = projects);
  }

  getGrades() {
    this.userService.getGrades()
        .subscribe(grades => this.grades = grades);
  }

  getDepartments() {
    this.userService.getDepartments()
        .subscribe(departments => this.departments = departments);
  }

  getUsers() {
    this.userService.getUsers()
        .subscribe(users => this.users = users);
  }

  // createNewProject(regData): ProjectArray {
  //   return {
  //     selectedProject: regData.selectedProject , from: null, to: null
  //   }
  // }

  onSubmit( registerData ) {
    if(!registerData) { return; }
    // this.createNewProject(registerData)
    // else if(registerData.selectedUserType === this.userTypes[3].viewValue) {
    //   this.userService.addSecurity( registerData as Security )
    //   .subscribe(security => {
    //     this.securities.push(security);
    //   });
    // }
    else {
    this.userService.addUser( registerData as User )
      .subscribe(user => {
        this.users.push(user);
      });
    }
      this.registerForm.reset();
      console.warn('Your details have been submitted', registerData);
  }


}

